@import './variables/variables';

.main__container {
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    color: #1B0835;
    caret-color: transparent;
}

.nav__menu {
    z-index: 10;
    position: fixed;
    padding-bottom: 15px;
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 500;
    caret-color: transparent;
}

.page_list {
    margin: 0;
    /* Обнуляем значение отступов */
    padding: 4px;
    /* Значение полей */

    &li {
        display: inline;
        /* Отображать как строчный элемент */
        margin-right: 5px;
        /* Отступ слева */
        border: 1px solid #000;
        /* Рамка вокруг текста */
        padding: 3px;
        /* Поля вокруг текста */
    }
}

.menu__link {
    text-decoration: none;
}
